const ItemMixins = {
  // Edible mixins
  Edible: {
    name: 'Edible',

    init: function(template) {
      // Number of points to add to hunger
      this._foodValue = template['foodValue'] || 5;
      // Number of times the item can be consumed
      this._maxConsumptions = template['consumptions'] || 1;
      this._remainingConsumptions = this._maxConsumptions;
      this._isOnGround = template['onGround'] || false;
      this._healAmount = template['healAmount'] || 0;
    },

    getFoodValue: function() {
      return this._foodValue;
    },

    getHealAmount: function() {
      return this._healAmount;
    },

    getGroundStatus: function() {
      return this._isOnGround;
    },

    setGroundStatus: function(status) {
      this._isOnGround = status;
    },

    listeners: {
      details: function() {
        return [{ key: 'food', value: this._foodValue }];
      },
    },

    eat: function(entity) {
      if (entity.hasMixin('FoodConsumer')) {
        if (this.hasRemainingConsumptions()) {
          entity.modifyFullnessBy(this._foodValue);
          if (this._healAmount > 0) {
            entity.heal(this._healAmount);
          }
          this._remainingConsumptions--;
        }
      }
    },

    hasRemainingConsumptions: function() {
      return this._remainingConsumptions > 0;
    },

    describe: function() {
      if (this._maxConsumptions !== this._remainingConsumptions) {
        return 'partly eaten ' + this.getName();
      } else {
        return this.getName();
      }
    },
  },

  Equippable: {
    name: 'Equippable',
    init: function(template) {
      this._attackValue = template['attackValue'] || 0;
      this._defenseValue = template['defenseValue'] || 0;
      this._wieldable = template['wieldable'] || false;
      this._wearable = template['wearable'] || false;
      this._rangedAttackValue = template['rangedAttackValue'] || 0;
      this._ranged = template['ranged'] || false;
      this._ammo = template['ammo'] || false;
    },

    listeners: {
      details: function() {
        let results = [];
        if (this._wieldable) {
          results.push({
            key: 'attack',
            value: this._ranged
              ? this.getRangedAttackValue()
              : this.getAttackValue(),
          });
        }
        if (this._wearable) {
          results.push({ key: 'defense', value: this.getDefenseValue() });
        }
        return results;
      },
    },

    getAttackValue: function() {
      return this._attackValue;
    },

    getDefenseValue: function() {
      return this._defenseValue;
    },

    isWieldable: function() {
      return this._wieldable;
    },

    isWearable: function() {
      return this._wearable;
    },

    getRangedAttackValue: function() {
      return this._rangedAttackValue;
    },

    isRanged: function() {
      return this._ranged;
    },

    getAmmo: function() {
      return this._ammo;
    },
  },

  Throwable: {
    name: 'Throwable',

    // default AV is different depending on item type
    // 1 for food etc, and attackValue for weapons
    _defaultAV: function(template) {
      return template['attackValue'] ? template['attackValue'] : 1;
    },

    init: function(template) {
      this._throwableAttackValue =
        template['throwableAttackValue'] || this._defaultAV(template);
    },

    listeners: {
      details: function() {
        return [{ key: 'throw damage', value: this._throwableAttackValue }];
      },
    },

    // standard getters
    getThrowableAttackValue: function() {
      return this._throwableAttackValue;
    },
  },

  Ammo: {
    name: 'Ammo',

    init: function(template) {
      this._count = template['count'] || 1;
    },

    listeners: {
      details: function() {
        return [{ key: 'count', value: this._count }];
      },
    },

    getCount: function() {
      return this._count;
    },

    setCount: function(count) {
      this._count = count;
    },

    addAmmo: function(count) {
      this._count += count;
    },

    removeAmmo: function(count) {
      this._count -= count;
    },
  },
};

export default ItemMixins;



